
import wx from 'weixin-js-sdk'
const pageSize = 10
import {mapActions } from 'vuex'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import Picker from 'vux/src/components/picker/index.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

const list = () => ['全部', '待付款', '待收货', '已完成', '已取消']
const dataList ={
'0':{max:0,list:null,t:1},
'1':{max:0,list:null,t:1},
'2':{max:0,list:null,t:1},
'3':{max:0,list:null,t:1},
'4':{max:0,list:null,t:1}
}
export default {
components:{
Tab, TabItem, Swiper, SwiperItem,LoadMore,Picker ,Confirm
},
data(){
return{
def:false,
listIndex:0,
slected:[''],
menus1:[[{name:'',value:''}]],
isOver:[true,true,true,true,true],
tip:['正在加载','正在加载','正在加载','正在加载','正在加载'],
isOvers:[true,true,true,true,true],
tips:['正在刷新','正在刷新','正在刷新','正在刷新','正在刷新'],
pageSize:pageSize,
onFetching:[false,false,false,false,false],
onFetchings:[false,false,false,false,false],
dataObj:dataList,
index: 0,
demo: '全部',
list:list(),
sy:'',
ey:'',
show1:false,
orderId:''
}
},
mounted(){
document.title = '我的订单'
if(this.$route.query.token){
localStorage.setItem('token',decodeURIComponent(this.$route.query.token))
}
if(this.$route.query.refreshToken){
localStorage.setItem('refreshToken',decodeURIComponent(this.$route.query.refreshToken))
}

if(this.$route.query.cs) {
this.demo = decodeURIComponent(this.$route.query.cs)
this.index = this.$route.query.csi
this.getList(0,this.demo)
}
else{
this.getList(0)
}
},
methods:{
...mapActions(['orderList','orderCancelReason','cancelOrder','deleteOrder','confirmReceipt']),
shopping(){
wx.miniProgram.switchTab({
url:'/pages/home/index'
})
},
sureOre(id){
this.showLoading()
this.confirmReceipt({data:{orderId:id},this:this,s:1,method:this.confirmReceipt})
},
mo(){
this.getList(0,this.demo)
},
onConfirm(orderId,l){
this.listIndex = l
this.showLoading()
this.deleteOrder({data:{orderId:orderId},this:this,method:this.deleteOrder})
},
delOd(orderId,l){
let _this = this
this.$vux.confirm.show({
content: '确认删除订单',
onCancel () {
},
onConfirm () {
_this.onConfirm(orderId,l)
}
})
},
goBack(){
this.hideLoading()
let ls = this.dataObj[this.index+''].list.slice(0)
ls.splice(this.listIndex,1)
this.dataObj[this.index+'']=Object.assign({},this.dataObj[this.index+''],{list:ls})
},
cancelOrd(id){
this.orderId = id
this.orderCancelReason({this:this})
this.show1 = true
},
setRe(data){
data = data.map(v => {
return {name:v.description,value:v.description}
})
this.menus1 = [data]
},
hxcz(){
this.showTost('取消订单成功')
this.getList(0,this.demo)
},
goCe(){
let data={
orderId:this.orderId,
operateReason:this.slected[0]
}

this.cancelOrder({data:data,this:this})
this.show1 = false
},
choseWhy(k){
console.log(k);
},
goDetail(obj){
this.def = true



this.$router.push({name:'orderDetail',query:{id:obj.orderId}})
},
qh(i,item){
this.demo = item || this.list[i]
this.index = i
if(this.demo == '全部'){
if(this.dataObj[this.index+''].t){
this.getList(0)
}
}
else{
if(this.dataObj[this.index+''].t){
this.getList(0,this.demo)
}
}

},
getList(n,orderStatusType,f){
this.hideTost()
this.showLoading()
if(!orderStatusType){
orderStatusType = ''
}
if(orderStatusType == '全部'){
orderStatusType = ''
}
this.orderList({method:this.orderList,this:this,size:f || '',data:{page:n,pageSize:this.pageSize,orderStatusType:orderStatusType}})
},
ontouchstart(e){
this.sy = e.changedTouches[0].pageY
},
ontouchend(e){
this.ey = e.changedTouches[0].pageY
if(this.sy - this.ey > 30){//上拉
this.bindScroll()
}
if(this.ey - this.sy > 50){//下拉
this.reFresh()
}
},
lijifk(obj){
wx.miniProgram.navigateTo({
url:'/pages/payGd/index?id='+obj.orderId +'&m=' +obj.grandTotal
})
},
concatData(arr){
this.onFetching.splice(this.index,1,false)
let ls = this.dataObj[this.index+''].list.slice(0)
ls.push(...arr)
this.dataObj[this.index+'']=Object.assign({},this.dataObj[this.index+''],{list:ls})
},
reFresh(){
if(this.onFetchings[this.index+'']){
return
}
let c= 'hdObj'+this.index
let obj=document.getElementsByClassName(c)[0]
let dh = obj.scrollTop
if(dh == 0){
this.onFetchings.splice(this.index,1,true)
this.getList(0,this.demo)
}

},
bindScroll(){
if(this.dataObj[this.index+''].max == 0){
return
}
if(this.dataObj[this.index+''].max == this.dataObj[this.index+''].list.length ){
this.onFetching.splice(this.index,1,true)
this.tip.splice(this.index,1,'没有更多了')
this.isOver.splice(this.index,1,false)
return
}
if(this.onFetching[this.index+'']){
return
}
let c= 'hdObj'+this.index
let obj=document.getElementsByClassName(c)[0]
let dh = obj.scrollTop
let h = obj.clientHeight
let cdf = 'contHt'+this.index
let bh = document.getElementsByClassName(cdf)[0].clientHeight
if(bh - dh <= h ){

this.onFetching.splice(this.index,1,true)
let n = Math.ceil(this.dataObj[this.index+''].list.length/this.pageSize)
let s= this.list[this.index+'']
let flag =true
this.getList(n,s,flag)
}
},
copyData(data){
this.onFetchings.splice(this.index,1,false)
data = {...data,t:0}
switch (data.status){
case '已取消':
this.dataObj['4'] = Object.assign({},data)
console.log(this.dataObj['4'])
break
case '已完成':
this.dataObj['3']= Object.assign({},data)
break
case '待付款':
this.dataObj['1'] = Object.assign({},data)
break
case '待收货':
this.dataObj['2'] = Object.assign({},data)
break
default:
this.dataObj['0'] = Object.assign({},data)
}
}
},
beforeDestroy(){
if(this.def){
return
}
},











}



